<template>
  <div>
    <input type="file" @change="readExcel()" /><button
      id="modeling-button"
      @click="modeling()"
    >
      
    </button>
  </div>
</template>

<script>
/* eslint-disable */
export default {
  name: "app",
  data() {
    return {
      house: [],
      medv: [],
    };
  },
  methods: {
    readExcel() {
      let input = event.target; // target?
      let reader = new FileReader(); // FileReader??
      reader.onload = function () {
        // onload?
        let data = reader.result;
        let wordBook = XLSX.read(data, { type: "binary" }); // ???
        const x = wordBook.Sheets.Data; //  sheet 
        for (let j = 2; j < 508; j++) {
          let line = [];
          let line2 = [];
          for (let i = 65; i < 78; i++) {
            let str = String.fromCharCode(i) + j;
            // fromCharCode()  DEC  Char .
            line.push(x[str].v);
            // A M .
          }
          line2.push(x["N" + j].v);
          house.push(line);
          medv.push(line2);
        }
      };
      reader.readAsBinaryString(input.files[0]);
      // ???
    },
    modeling(_) {
      console.log(house);
      let xt = tf.tensor(house); //    ..  tf.tensor  ?
      let yt = tf.tensor(medv);

      //   
      let X = tf.input({ shape: [13] }); //   
      let Y1 = tf.layers.dense({ units: 13, activation: "relu" }).apply(X); // apply  
      let Y2 = tf.layers.dense({ units: 13, activation: "relu" }).apply(Y1);
      let Z = tf.layers.dense({ units: 1 }).apply(Y2);
      let model = tf.model({ inputs: X, outputs: Z }); //  
      let compileParam = {
        optimizer: tf.train.adam(), // train ??
        loss: tf.losses.meanSquaredError,
      }; //  loss 
      model.compile(compileParam); // 
      const fitParm = {
        epochs: 3000,
        callbacks: {
          onEpochEnd: function (epoch, logs) {
            console.log("epoch", epoch, logs, "RMSE=>", Math.sqrt(logs.loss));
            // RMSE ? sprt ?
          },
        },
      };
      // const my = tf.tensor([32]);
      model.fit(xt, yt, fitParm).then((_) => {
        // model.fit(xt, yt, fitParm) =>     . =>    
        let result = model.predict(xt);
        // model.predict(my) =>      .
        result.print();
        // model.save("downloads://my-model"); //  
        model.save("localstorage://my-model-1");
        console.log(" ");
      });
    },
  },
};
</script>

